import { MainView } from "../pages/main";
import { NewPostView } from "../pages/newPost";
import { Api } from "./api";

export class Router {
  static route() {
    const routePath = location.hash;

    if (routePath === "") {
      Api.get("posts")
        .then(data => {
          let length = data.data.posts.length;
          let posts = data.data.posts;
          MainView.render(length, posts);
          console.log("ÏÑ±Í≥µ", data);
        })
        .catch(error => console.log("ÏóêÎü¨", error));
    } else if (routePath.indexOf("/postlist/") >= 0) {
      // Ìï¥Îãπ Ìè¨Ïä§Ìä∏ÎÇ¥Ïö© Ïó¨Îäî Ìï®Ïàò Íµ¨ÌòÑ
      console.log('ÎØ∏Íµ¨ÌòÑ')
    } else if (routePath.indexOf("/newpost") >= 0) {
      NewPostView.render();
    }
  }
}